<template>
    <div>
        <jumbotron header="Admin- und Sync-Benutzer"
                     lead="Konfigurieren Sie die n√∂tigen Zugangsdaten"
                     content="">
        </jumbotron>
        <div class="row">
            <div class="col">
                <div class="form-group">
                    <div class="input-group">
                        <span class="input-group-addon fixed-addon"><strong>Admin-Benutzer</strong></span>
                        <b-form-input size="35" required v-model="admin.name" type="text" :state="admin.name.length ? 'success' : 'warning'"></b-form-input>
                        <span class="input-group-addon">
                        <icon name="home"></icon>
                    </span>
                    </div>
                </div>
                <div class="form-group">
                    <div class="input-group">
                        <span class="input-group-addon fixed-addon"><strong>Admin-Passwort &nbsp; <a @click="admin.pass = this.generatePassword()"><icon name="refresh"></icon></a></strong></span>
                        <b-form-input size="35" required v-model="admin.pass" type="text" :state="admin.pass.length ? 'success' : 'warning'"></b-form-input>
                        <span class="input-group-addon">
                        <icon name="lock"></icon>
                    </span>
                    </div>
                </div>
                <hr>
            </div>
        </div>
        <div class="row">
            <div class="col">
                <div class="form-group">
                    <div class="input-group">
                        <span class="input-group-addon fixed-addon"><strong>Sync-Benutzer</strong></span>
                        <b-form-input size="35" required v-model="wawi.name" type="text" :state="wawi.name.length ? 'success' : 'warning'"></b-form-input>
                        <span class="input-group-addon">
                        <icon name="home"></icon>
                    </span>
                    </div>
                </div>
                <div class="form-group">
                    <div class="input-group">
                        <span class="input-group-addon fixed-addon"><strong>Sync-Passwort &nbsp; <a @click="wawi.pass = this.generatePassword()"><icon name="refresh"></icon></a></strong></span>
                        <b-form-input size="35" required v-model="wawi.pass" type="text" :state="wawi.pass.length ? 'success' : 'warning'"></b-form-input>
                        <span class="input-group-addon">
                        <icon name="lock"></icon>
                    </span>
                    </div>
                </div>
            </div>
        </div>
        <continue :cb="saveUsers"></continue>
    </div>
</template>

<script>
    export default {
        name: 'adminuser',
        data() {
            return {
                admin: {
                    name: 'admin',
                    pass: this.generatePassword()
                },
                wawi:  {
                    name: 'sync',
                    pass: this.generatePassword()
                }
            };
        },
        methods: {
            saveUsers() {
                this.$store.commit('setAdminUser', this.admin);
                this.$store.commit('setWawiUser', this.wawi);
                return this.admin.name.length > 0
                    && this.admin.pass.length > 0
                    && this.wawi.name.length > 0
                    && this.wawi.pass.length > 0;
            },
            generatePassword() {
                let crypto = window.crypto || window.msCrypto,
                    buf    = new Uint8Array(9);
                if (typeof crypto !== 'undefined') {
                    return btoa(String.fromCharCode.apply(null, crypto.getRandomValues(buf)));
                }
                return '';
            }
        }
    };
</script>
<style scoped>
    .input-group-addon.fixed-addon {
        width: 170px;
        text-align: right;
    }
    a {
        cursor: pointer;
    }
</style>


