<template>
    <div>
        <b-progress v-model="installProgress" :precision="1" show-progress animated class="mb-3 mt-3 hidden-print"></b-progress>

        <div id="install-wrapper">
            <installation-summary class="steps" id="step-7" v-if="step === 7"></installation-summary>

            <schema class="steps" id="step-6" v-if="step === 6"></schema>

            <admin-user class="steps" id="step-5" v-if="step === 5"></admin-user>

            <database-parameters class="steps" id="step-4" v-if="step === 4"></database-parameters>

            <server-check class="steps" id="step-2" v-if="step === 3"></server-check>

            <directory-check class="steps" id="step-2" v-if="step === 2"></directory-check>

            <installed-check class="steps" id="step-1" v-if="step === 1"></installed-check>

            <hello class="steps" id="step-2"  v-if="step === 0"></hello>
        </div>
    </div>
</template>

<script>
    import PageHeader from './Header';
    import InstalledCheck from './InstalledCheck';
    import Hello from './Hello';
    import ServerCheck from './ServerCheck';
    import DirectoryCheck from './DirectoryCheck';
    import DatabaseParameters from './DatabaseParameters';
    import AdminUser from './AdminUser';
    import Schema from './Schema';
    import InstallationSummary from './Summary';
    export default {
        name:       'installer',
        components: {
            PageHeader,
            InstalledCheck,
            Hello,
            DirectoryCheck,
            ServerCheck,
            DatabaseParameters,
            AdminUser,
            Schema,
            InstallationSummary
        }
    };
</script>

<style scoped>
    .list-group {
        width: 100%;
    }
</style>



