import Vue from 'vue';
import Router from 'vue-router';
import Installer from '@/components/Installer';

Vue.use(Router);

export default new Router({
    mode:   'history',
    routes: [
        {
            path:      '/',
            name:      'Installer',
            component: Installer
        }
    ]
});



