<template>
    <div>
        <jumbotron header="Dateirechte"
                     lead="Pr√ºft, ob alle n√∂tigen Verzeichnisse beschreibbar sind"
                     content="">
        </jumbotron>
        <div class="row">
            <div class="col" v-if="checkedDirectories">
                <b-card show-header no-block>
                    <h3 slot="header">
                        <span class="badge" :class="{'badge-danger': directoriesStatus === 2, 'badge-warning': directoriesStatus === 1, 'badge-success': directoriesStatus === 0}">
                            <icon name="check" v-if="directoriesStatus === 0"></icon>
                            <icon name="exclamation-triangle" v-else></icon>
                        </span> Schreibrechte <b-btn v-b-toggle="'collapse-directories'" size="sm">anzeigen</b-btn>
                    </h3>
                    <b-collapse id="collapse-directories">
                        <b-list-group class="list-group-flush">
                            <b-list-group-item v-for="dir in directories" :key="dir.idx">
                            <span class="badge" :class="{'badge-success': dir.isWriteable === true, 'badge-danger': dir.isWriteable === false}">
                                <icon name="check" v-if="dir.isWriteable === true"></icon>
                                <icon name="exclamation-triangle" v-else></icon>
                            </span>
                                <span class="ml-2">{{ dir.dir }}</span>
                            </b-list-group-item>
                        </b-list-group>
                    </b-collapse>
                </b-card>
            </div>
        </div>
        <b-alert variant="info" show v-if="!checkedDirectories">
            <icon name="refresh" spin></icon> Pr√ºfe Schreibrechte...
        </b-alert>
        <b-alert variant="danger" show v-if="networkError !== false">
            <icon name="exclamation-triangle"></icon> Netzwerkfehler: {{ networkError }}
        </b-alert>
        <continue :disable="!checkedDirectories || networkError !== false"></continue>
    </div>
</template>

<script>
    import axios from 'axios';
    export default {
        name:  'directorycheck',
        data() {
            let directories        = [],
                networkError       = false,
                directoriesStatus  = 0,
                checkedDirectories = false;
            axios.get(this.$getApiUrl('dircheck'))
                .then(response => {
                    Object.keys(response.data.testresults).forEach((dir, idx) => {
                        this.directories.push({
                            idx,
                            dir,
                            isWriteable: response.data.testresults[dir]
                        });
                    });
                    /* eslint-disable no-confusing-arrow */
                    this.directoriesStatus  = this.directories.reduce((acc, val) => !val.isWriteable || val === 1 ? 1 : 0, 0);
                    this.checkedDirectories = true;
                })
                .catch(error => {
                    this.networkError = error.response
                        ? error.response
                        : `URL ${this.$getApiUrl('dircheck')} nicht erreichbar.`;
                });
            return {
                directories,
                directoriesStatus,
                checkedDirectories,
                networkError
            };
        }
    };
</script>



