<template>
    <div>
        <jumbotron header="Datenbankparameter"
                     lead="Konfigurieren Sie die Datenbank"
                     content="<p>F√ºr die Installation des JTL-Shops ben√∂tigen wir eine MySQL-Datenbank.</p>
<p>Meistens m√ºssen der Benutzer und die Datenbank erst manuell erstellt werden. Bei Problemen wenden Sie sich bitte an Ihren Administrator bzw. Webhoster, da dieser Vorgang von Hoster zu Hoster unterschiedlich ist und von der eingesetzten Software abh√§ngt.</p>
<p>Der Benutzer ben√∂tigt Lese-, Schreib- und L√∂schrechte (Create, Insert, Update, Delete) f√ºr diese Datenbank.</p>
<p>Als <strong>Host</strong> ist <i>localhost</i> zumeist die richtige Einstellung. Diese Information erhalten Sie ebenfalls von Ihrem Webhoster.</p>
        <p>Das Feld <strong>Socket</strong> f√ºllen Sie bitte nur aus, wenn Sie ganz sicher sind, dass Ihre Datenbank √ºber einen Socket erreichbar ist. In diesem Fall tragen Sie bitte den absoluten Pfad zum Socket ein.</p>">
        </jumbotron>
        <div class="row">
            <div class="col">
                <div class="form-group">
                    <div class="input-group">
                        <span class="input-group-addon fixed-addon"><strong>Host</strong></span>
                        <b-form-input size="35" required v-model="db.host" type="text" placeholder="Datenbank-Host" :state="db.host.length?'success':'warning'"></b-form-input>
                        <span class="input-group-addon">
                        <icon name="home"></icon>
                    </span>
                    </div>
                </div>
                <div class="form-group">
                    <div class="input-group">
                        <span class="input-group-addon fixed-addon"><strong>Socket (optional)</strong></span>
                        <b-form-input size="35" v-model="db.socket" type="text" placeholder="Socket (z.B. /tmp/mysql5.sock)" :state="db.socket.length?'success':'warning'"></b-form-input>
                        <span class="input-group-addon">
                        <icon name="exchange"></icon>
                    </span>
                    </div>
                </div>
                <div class="form-group">
                    <div class="input-group">
                        <span class="input-group-addon fixed-addon"><strong>Benutzername</strong></span>
                        <b-form-input size="35" required v-model="db.user" type="text" placeholder="Datenbank-Benutzername" :state="db.user.length?'success':'warning'"></b-form-input>
                        <span class="input-group-addon">
                        <icon name="user"></icon>
                    </span>
                    </div>
                </div>
                <div class="form-group">
                    <div class="input-group">
                        <span class="input-group-addon fixed-addon"><strong>Passwort</strong></span>
                        <b-form-input size="35" required v-model="db.pass" type="text" placeholder="Datenbank-Passwort" :state="db.pass.length?'success':'warning'"></b-form-input>
                        <span class="input-group-addon">
                        <icon name="lock"></icon>
                    </span>
                    </div>
                </div>
                <div class="form-group" style="margin-bottom: 0;">
                    <div class="input-group">
                        <span class="input-group-addon fixed-addon"><strong>Datenbank-Name</strong></span>
                        <b-form-input size="35" required v-model="db.name" type="text" placeholder="Datenbank-Name" :state="db.name.length?'success':'warning'"></b-form-input>
                        <span class="input-group-addon">
                        <icon name="database"></icon>
                    </span>
                    </div>
                </div>
                <hr>
                <b-btn size="md" variant="primary" @click="checkCredentials(db)">
                    <icon name="refresh"></icon> Daten pr√ºfen
                </b-btn>
            </div>
        </div>
        <div class="result mt-3" v-if="error !== null">
            <b-alert :variant="error ? 'danger' : 'success'" show>
                <icon :name="error ? 'exclamation-triangle' : 'check'"></icon> {{ msg }}
            </b-alert>
        </div>
        <continue :disable="error !== false"></continue>
    </div>
</template>

<script>
    import axios from 'axios';
    import qs from 'qs';
    export default {
        name: 'databaseparameters',
        data() {
            let msg   = null,
                error = null;
            return {
                db: {
                    host:   'localhost',
                    pass:   'shop4',
                    socket: '',
                    user:   'shop4',
                    name:   'empty'
                },
                error,
                msg
            };
        },
        methods: {
            checkCredentials(db) {
                axios.post(this.$getApiUrl('credentialscheck'), qs.stringify(db))
                    .then(response => {
                        this.msg = response.data.msg;
                        this.error = response.data.error;
                        this.$store.commit('setDBCredentials', db);
                    })
                    .catch(error => {
                        this.msg   = error.response
                            ? error.response
                            : `URL ${this.$getApiUrl('credentialscheck')} nicht erreichbar.`;
                        this.error = true;
                    });
            }
        }
    };
</script>
<style scoped>
    .input-group-addon.fixed-addon {
        width: 150px;
        text-align: right;
    }
</style>


